.button, button {
  font: inherit;
  font-weight: 400;
  background: #95a5a6;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  color: #fff;
  transition: background .5s ease, transform .5s ease;
  margin: 10px;
  display: inline-block;
  text-decoration: none; }
  .button:hover, button:hover {
    background: #b1bdbd;
    transform: translate3d(0, 4px, 0); }
  .button > *, button > * {
    pointer-events: none; }
  .button.alt, button.alt {
    margin: 10px 15px 20px;
    background: #9b59b6; }
    .button.alt:hover, button.alt:hover {
      background: #b07cc6; }
  .button.warn, button.warn {
    background: #e74c3c; }
    .button.warn:hover, button.warn:hover {
      background: #ed7669; }

.marking {
  transition: background .5s ease;
  background: #2ecc71; }

.deleted {
  transition: background .5s ease;
  background: none !important; }

.marking .marking {
  background: #25a25a; }

.marking .marking .marking {
  background: #1b7943; }

::selection {
  background: #2ecc71; }

